<script>
import { mapActions, mapGetters } from "vuex";
import Header from '../../components/Header.vue'

export default {
  name: 'Minesweeper',
  components: { Header },
  data() {
    return {
      customWidth: 11,
      customHeight: 24,
      makingCustom: false,
      darkTheme: true,
      tweakingSettings: false,
      allFlags: false,
      animationsEnabled: true,
      canClickFlag: false,
    }
  },
  computed: {
    ...mapGetters("Minesweeper", ["getGlagsPlaced", "getGrid", "gameOver", "gamesPlayed", "isEasy", "isNormal", "isHard", "isCustom", "gameStarted", "minesLeft", "gameWon", "getWins", "getLosses","getWidth", "getHeight", "getDifficulty", "getMines", "getTimePassed"])
  },
  methods: {
    ...mapActions("Minesweeper", ["startNewGame", "toggleFlag", "clickSpot", "makeFirstClick", "flagAll", "setCanClickFlag"]),
  },
  mounted() {
    this.flagAll(this.allFlags)
    this.setCanClickFlag(this.canClickFlag)
    let easyDimensions = 8
    let normalDimensions = 10
    let hardDimensions = 12
    let difficulty = 'Easy'
    if(this.$route.query.difficulty) {
      switch(this.$route.query.difficulty) {
        case 'Easy':
          this.startNewGame({ width: easyDimensions, height: easyDimensions, difficulty: 'Easy' })
          break    
        case 'Normal':
          this.startNewGame({ width: normalDimensions, height: normalDimensions, difficulty: 'Normal' })
          break
        case 'Hard':
          this.startNewGame({ width: hardDimensions, height: hardDimensions, difficulty: 'Hard' })
          break
      }
    } else {
      let mines = -1
      let width = normalDimensions
      let height = normalDimensions
      if(!isNaN(this.$route.query.width) && !isNaN(this.$route.query.width)) {
        let tmpWidth = Number(this.$route.query.width)
        let tmpHeight = Number(this.$route.query.height)
        if(tmpWidth <= 100 && tmpHeight <= 100 && tmpWidth >= easyDimensions && tmpHeight >= easyDimensions) {
          width = tmpWidth
          height = tmpHeight
          difficulty = 'Custom'
        }
      }
      if(!isNaN(this.$route.query.mines)) {
        mines = Number(this.$route.query.mines);
        difficulty = 'Custom'
      }
      this.startNewGame({ width, height, difficulty, mines })
    }
  },
}
</script>

